        <h2>Problem Statement</h2>

        <p>Earlier this year, Rackspace faced several user experience challenges, which included:</p>

        <p><strong>Inconsistent UI</strong> Different product teams had been operating with varying design aesthetics and
          styles, resulting in a disjointed user experience.</p>

        <p><strong>Increased Development Time</strong> Designers and developers were dedicating significant time
