        <p><strong>Scalability Issues</strong> Rackspace had many UI dev teams across the company. Our design team could
          not support the number of developers and these developers needed something easy to implement.</p>

        <h2>Approach</h2>

        <h3>Research and Analysis</h3>

        <p>Our team conducted extensive user research, including surveys and interviews, to gain deep insights into user
          pain points and preferences and a comprehensive audit of existing design assets was carried out, identifying
          redundancies and inconsistencies. We identified over 4 previous design system and UI frameworks that were
          created/developed in the void of an easy to use out of the box design system solution. Speicifcally, we
          highlighted the need for a react bootstrap theme to provide a &quot;turn-key&quot; solution for our development
          teams that are working on Fabric Platform UIs.</p>

        <h3>Milestone 1</h3>

        <p><strong>Design Tokens</strong>Our first milestone was to define the design tokens that could be used for
          any framework to implement the Fabric Design System for any UI at Rackspace. We started with color and
          typography as the critical foundations for the most broad visual consistency.</p>

        <p><strong>React Bootstrap Theme</strong>The team then used these tokens and applied them to React Bootstrap
          framework to enable any teams that currently use React Bootstrap to adopt the design system as quickly as
          possible with the least amount of development work. Design did an initial, &quot;first-pass&quot; audit on
          components to ensure there were no major showstoppers. We found some around clarity for buttons vs labels and
          suggested development changes to resolve.</p>

        <p><strong>Fabric Components</strong>Bootstrap provides a great foundation to start our component list. However,
          we found Rackspace has some use-cases that were not accounted for, but are common across multiple UIs. We
          started with header and footer components, and then moved to zero state page layouts. Some of the smaller
          components to be developed are around key value pairs and stats.</p>

        <p><strong>Documentation and Maintenance</strong>To ensure the longevity of the design system, we meticulously
          documented design guidelines, usage instructions, and best practices. This included a <a
