          a <a href="https://manage.rackspace.com/rackspace-fabric-design-system/?path=/docs/home--docs">Storybook</a>
          site for development documentation.</p>

        <h3>Milestone 2</h3>

        <p><strong>Design Tokens</strong>Milestone 2 design tokens included spacing, box-shadows, and border radius.</p>

        <p><strong>React Bootstrap Theme</strong> Designers did a full audit of the bootstrap theme and highlight changes
          needed to the bootstrap theme to resolve smaller issues not prioritized in milestone 1.</p>

        <p><strong>Fabric Components</strong>Additional components are being designed and developed including list page
          and detail page layouts. Also a smaller component called Transfer Lists needed by multiple UIs for selecting
          multiple items from a larger list.</p>

