    <div class="container">
      
      <div class="col-sm-12 col-lg-9">
      <p>Early 2023, Rackspace Technology made a commitment and pivot to support and drive more services and less infrastructure. Multiple teams activated and began making varying service experiences. Product leadership, principal designer, and I were tasked with creating a strategy and plan for incorporating these experiences together and creating consistency across them.
      </p>

      <h2>Problem Statement:</h2>

      <p><strong>Inconsistent Service Experience:</strong> Different teams were creating products and services
        indipendently. As a user moved between services, their experience would vary widely on features and capabilities
        available.</p>

      <p><strong>Increased Development Time:</strong> With no established patterns and strategy, teams were starting from
        scratch with every new service or product developed.</p>

      <p><strong>Unclear Value Proposition:</strong> Rackspace was not well positioned to showcase the value the company
        provided to customers. It was difficult to understand and see what Rackspace was doing for a customer in the
        digital experience.</p>

      <h2>Approach</h2>

      <h3>Research and Analysis</h3>

      <p>Our team conducted heurstic analysis of all our existing service platforms to identify common tasks and
        workflows. We then highlighted where these experiences differed the most. Some products do offer more capabilities
        and self-service in the portal than others and we had to account for this in our strategy and plan.</p>

      <h3>Milestone 1</h3>

      <p><strong>Design System Implementation:</strong>The first goal was to have all the experiences consuming the
        design system to ensure visual consistency.</p>

      <p><strong>Common Experience Flows:</strong> The team identified 3 key experience flows that almost all the products
        and services had. Viewing current status, changing the status, removing items from coverage. We wanted to get crystal clear on these flows and how we defined them to ensure these were consistent regardless of service portal.</p>

      <p style="margin-left:40px"><span class="example">Example: Service Enrollment allowed users to add services to a sub-set of accounts, and
        Resources view allowed to add/remove services on individual resources.</span></p>

      <p><strong>Circle Of Life Diagram:</strong>We termed this the &quot;Circle of Life Diagram&quot; because of the way
        it looked. In reality it defined what Rackspace&#39;s competitive advantage was and what experiences we wanted to
        hold sacred for our customers. This drove us to really focus on the connection between service providers and allow
        users the ability to use the provider&#39;s consoles when needed.</p>

      <p style="margin-left:40px"><span class="example">Example: We are not going to provide a way to create a terraform workflow. However, we do want to control the service experience a user will have when receiving support during an incident.</span></p>

      <h3>Milestone 2</h3>

      <p><strong>UI implementation:</strong>Resource view, Patching view, Service Enrollment</p>

      <p>Most of this is unreleased at this moment, but I can provide visual examples of these in a private setting.</p>

      <h2>Results</h2>

      <p><strong>Consistent User Experience</strong>Regardless of what UI the user is in, there is a consistent
        experience. Not just in the look and feel, but even in the small interactions when performing an action.&nbsp;</p>

      <p><strong>Clear Strategy</strong>Using our north star &quot;circle of life diagram&quot; we were able to
        galvanize product teams with an understanding of what we can focus on to have the largest impact for our
        customers.</p>

    <p><strong>Scalability</strong>Leveraging the design system, we were able to create fabric components for widely
      used interactions that developers used to have to develop from scratch.</p>

    <h2>Conclusion</h2>

    <p>Creating a vision and strategy that gave clarity to how the company goals manifested in the portal experience
      allowed all our developers, designers, and product teams to drive together towards the same goal. We are still in
      this process, but we think we have laid the groundwork to ensure success in the future.</p>

